import { Form, Button } from 'react-bootstrap';
import React from "react";

function Register(){

  return  <div className='container  '>
   <h1 style={{color:"#85C88A", textAlign:"center"}}>Register</h1>
<Form className='registerForm'>
<Form.Group className="mb-3" controlId="fName" >
<Form.Label>
   First Name
</Form.Label>
<Form.Control type='fName' placeholder='First Name'></Form.Control>
      </Form.Group>

      <Form.Group className="mb-3" controlId="LName" >
<Form.Label>
    Last Name
</Form.Label>
<Form.Control type='lName' placeholder='Last Name'></Form.Control>
      </Form.Group>


      <Form.Group className="mb-3" controlId="email" >
<Form.Label>
    Email Address
</Form.Label>
<Form.Control type='email' placeholder='Email Address'></Form.Control>
      </Form.Group>
      <Form.Group className="mb-3" controlId="password" >
<Form.Label>
    Password
</Form.Label>
<Form.Control type='password' placeholder='Password'></Form.Control>
      </Form.Group>
      <Button  type="submit">
   Register
  </Button>
  </Form>  
  </div>
}

export default Register;

