import React from "react";
import {NavDropdown} from 'react-bootstrap';

function NavbarDropDown(props){
    return <NavDropdown menuVariant="dark" title={props.title}>
        
        <NavDropdown.Item href="#action/3.1">{props.one}</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.2">{props.two}</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.3">{props.three}</NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item href="#action/3.4">{props.four}</NavDropdown.Item>
        
    </NavDropdown>

    
}
export default NavbarDropDown;