import React from "react";
import Navbar from 'react-bootstrap/Navbar';

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import NavbarDropDown from "./NavbarDropDown";
import {LinkContainer} from 'react-router-bootstrap'


function Header(){
    return <header>
      <Navbar  variant="dark"  expand="lg">
  <Container>
  <LinkContainer to="/"><Navbar.Brand className="nav-brand" >Spices</Navbar.Brand></LinkContainer>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto">
        <LinkContainer to="/home"><Nav.Link >Home</Nav.Link></LinkContainer>
        <LinkContainer to="/offerings"><Nav.Link>Offerings</Nav.Link></LinkContainer>
       <LinkContainer to="/about"><Nav.Link>About Us</Nav.Link></LinkContainer> 
        <NavbarDropDown title="Products" one="Basic spice powders" two="Ready to Mix" three="Homemade Snacks"/>
        <div className="contact-btn"><a href="/login"> <Button className="button" variant="outline-light" size="lg" >Login</Button></a>
        <Button className="button" variant="outline-light" size="lg">Contact Us</Button></div>
        
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
    </header>
    
    
  
}

export default Header;

 